<template>
  <div>
    <div class="page-title">
      <h3>История записей</h3>
    </div>

    <div class="history-chart">
      <canvas></canvas>
    </div>
    <Loader v-if="loading"/>
    <p class="center" v-else-if="!records.length" ><router-link to="/record">Созадть первую запись</router-link></p>
    <section v-else>
     <HistoryTable :records="records"/>
    </section>
  </div>
</template>

<script>
import HistoryTable from '@/components/HistoryTable';

export default {
  name:'history',
  data:()=>({
    loading: true,
    records: [],
    categorys: [],
  }),
  async mounted(){
    this.categories = await this.$store.dispatch('fetchCategories')
    const records = await this.$store.dispatch('fetchRecords') //Уменшьшам количества ререндерига шаблона
  
    this.records = records.map(rec => {return{
      ...rec,
      categoryName: this.categories.find(cat => cat.id === rec.categoryID).title,
      class: rec.type === 'income'? 'green':'red',
      text: rec.type=== 'income'? 'Доход':'Расход',
    }})
    this.loading = false;
  },
  components:{HistoryTable},
}
</script>